import React, { useState, useEffect } from 'react'
import { 
  KeyIcon, 
  CheckIcon, 
  XMarkIcon,
  EyeIcon,
  EyeSlashIcon
} from '@heroicons/react/24/outline'

const SettingsPage = () => {
  const [apiKey, setApiKey] = useState('')
  const [isKeyVisible, setIsKeyVisible] = useState(false)
  const [isSaving, setIsSaving] = useState(false)
  const [saveStatus, setSaveStatus] = useState(null)
  const [errorMessage, setErrorMessage] = useState('') // 'success', 'error', null
  const [hasExistingKey, setHasExistingKey] = useState(false)

  // API
  useEffect(() => {
    checkExistingKey()
  }, [])

  const checkExistingKey = async () => {
    try {
      const response = await fetch(`${import.meta.env.VITE_API_BASE_URL || ''}/api/ai/status`)
      const data = await response.json()
      setHasExistingKey(data.hasApiKey)
    } catch (error) {
      console.error('API:', error)
    }
  }

  const handleSaveKey = async () => {
    if (!apiKey.trim()) {
      setSaveStatus('error')
      setTimeout(() => setSaveStatus(null), 3000)
      return
    }

    setIsSaving(true)
    try {
      const response = await fetch(`${import.meta.env.VITE_API_BASE_URL || ''}/api/ai/set-key`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ apiKey: apiKey.trim() })
      })

      if (response.ok) {
        setSaveStatus('success')
        setHasExistingKey(true)
        setApiKey('')
        setErrorMessage('')
        setTimeout(() => setSaveStatus(null), 3000)
      } else {
        const errorData = await response.text()
        setSaveStatus('error')
        setErrorMessage(errorData || '')
        setTimeout(() => {
          setSaveStatus(null)
          setErrorMessage('')
        }, 5000)
      }
    } catch (error) {
      console.error('API:', error)
      setSaveStatus('error')
      setErrorMessage('')
      setTimeout(() => {
        setSaveStatus(null)
        setErrorMessage('')
      }, 5000)
    } finally {
      setIsSaving(false)
    }
  }

  const handleRemoveKey = async () => {
    if (!confirm('APIAI')) {
      return
    }

    setIsSaving(true)
    try {
      const response = await fetch(`${import.meta.env.VITE_API_BASE_URL || ''}/api/ai/remove-key`, {
        method: 'DELETE'
      })

      if (response.ok) {
        setSaveStatus('success')
        setHasExistingKey(false)
        setErrorMessage('')
        setTimeout(() => setSaveStatus(null), 3000)
      } else {
        const errorData = await response.text()
        setSaveStatus('error')
        setErrorMessage(errorData || '')
        setTimeout(() => {
          setSaveStatus(null)
          setErrorMessage('')
        }, 5000)
      }
    } catch (error) {
      console.error('API:', error)
      setSaveStatus('error')
      setErrorMessage('')
      setTimeout(() => {
        setSaveStatus(null)
        setErrorMessage('')
      }, 5000)
    } finally {
      setIsSaving(false)
    }
  }

  return (
    <div className="max-w-2xl mx-auto p-6">
      <div className="bg-white rounded-lg shadow-md border border-accent-200">
        {/*  */}
        <div className="p-6 border-b border-accent-200">
          <div className="flex items-center space-x-3">
            <KeyIcon className="w-6 h-6 text-primary-700" />
            <div>
              <h1 className="text-xl font-semibold text-accent-900">API</h1>
              <p className="text-sm text-accent-600 mt-1">
                Kimi APIAI
              </p>
            </div>
          </div>
        </div>

        {/*  */}
        <div className="p-6">
          {/*  */}
          <div className="mb-6">
            <div className="flex items-center space-x-2 mb-2">
              <span className="text-sm font-medium text-accent-700">:</span>
              {hasExistingKey ? (
                <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                  <CheckIcon className="w-3 h-3 mr-1" />
                  
                </span>
              ) : (
                <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-red-100 text-red-800">
                  <XMarkIcon className="w-3 h-3 mr-1" />
                  
                </span>
              )}
            </div>
            <p className="text-xs text-accent-500">
              {hasExistingKey 
                ? 'APIAI' 
                : 'APIAI'
              }
            </p>
          </div>

          {/* API */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-accent-700 mb-2">
              Kimi API
            </label>
            <div className="relative">
              <input
                type={isKeyVisible ? 'text' : 'password'}
                value={apiKey}
                onChange={(e) => setApiKey(e.target.value)}
                placeholder="sk-..."
                className="w-full px-3 py-2 pr-10 border border-accent-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-600 focus:border-transparent shadow-sm"
              />
              <button
                type="button"
                onClick={() => setIsKeyVisible(!isKeyVisible)}
                className="absolute inset-y-0 right-0 pr-3 flex items-center"
              >
                {isKeyVisible ? (
                  <EyeSlashIcon className="w-4 h-4 text-accent-400" />
                ) : (
                  <EyeIcon className="w-4 h-4 text-accent-400" />
                )}
              </button>
            </div>
            <p className="text-xs text-accent-500 mt-1">
              API
            </p>
          </div>

          {/*  */}
          <div className="flex space-x-3">
            <button
              onClick={handleSaveKey}
              disabled={isSaving || !apiKey.trim()}
              className="flex-1 bg-primary-700 text-white px-4 py-2 rounded-md hover:bg-primary-800 disabled:opacity-50 disabled:cursor-not-allowed transition-colors shadow-sm"
            >
              {isSaving ? '...' : (hasExistingKey ? '' : '')}
            </button>
            
            {hasExistingKey && (
              <button
                onClick={handleRemoveKey}
                disabled={isSaving}
                className="px-4 py-2 border border-red-300 text-red-700 rounded-md hover:bg-red-50 disabled:opacity-50 disabled:cursor-not-allowed transition-colors shadow-sm"
              >
                
              </button>
            )}
          </div>

          {/*  */}
          {saveStatus && (
            <div className={`mt-4 p-3 rounded-md ${
              saveStatus === 'success' 
                ? 'bg-green-50 text-green-800 border border-green-200' 
                : 'bg-red-50 text-red-800 border border-red-200'
            }`}>
              {saveStatus === 'success' 
                ? '' 
                : (errorMessage || '')
              }
            </div>
          )}

          {/*  */}
          <div className="mt-8 p-4 bg-accent-50 rounded-md border border-accent-100">
            <h3 className="text-sm font-medium text-accent-900 mb-2"></h3>
            <ul className="text-xs text-accent-600 space-y-1">
              <li>  <a href="https://platform.moonshot.cn/console/api-keys" target="_blank" rel="noopener noreferrer" className="text-primary-700 hover:underline">Moonshot AI</a> API</li>
              <li> API "sk-" </li>
              <li> </li>
              <li> AIkimi-k2-turbo-preview</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  )
}

export default SettingsPage